<template>
	<view class="toast" v-show="show" @click="open">
		<text>{{massage}}</text>
		<image src="../../static/tarbar/order_on.png" mode="widthFix"></image>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				show:false
			}
		},
		props: {
			massage: {
				type: [Number, String],
			},
			time:2000
		},
		methods:{
			open(){
				setTimeout(function(){
					this.show = true
				},time)
			}
		}
	}
</script>

<style lang="less">
	.toast{
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		display: flex;
		align-items: center;
		flex-flow: column;
		image{
			width: 150rpx;
		}
	}
</style>